<template>
  <v-app>
    <v-toolbar dense>
      <v-toolbar-title>Time {{ time }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn @click="addUserDialog = true" icon>
        <v-icon>mdi-account-group</v-icon>
      </v-btn>

      <v-btn @click="settingsDialog = true" icon>
        <v-icon>mdi-settings</v-icon>
      </v-btn>
    </v-toolbar>
    <v-card class="main-container" dark flat tile>
      <v-snackbar v-model="snackbar" :color="snackbarColor" top>
        {{ snackbarText }}
      </v-snackbar>

      <v-dialog v-model="addUserDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Add User</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <form v-on:submit.prevent @submit="onAddUser">
                <v-text-field v-model="name" label="Name" required></v-text-field>
                <v-row>
                  <v-col v-for="color in colors" :key="color" sm="2" xs="3">
                    <div :style="userColor === color ? { height: '56px', width: '56px', border: 'solid white 4px', 'border-radius': '100%' } : {}">
                      <v-avatar @click="userColor = color" :color="color"></v-avatar>
                    </div>
                  </v-col>
                </v-row>
              </form>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-btn class="mx-auto" color="primary" @click="onAddUser">Save user</v-btn>
          </v-card-actions>
          <v-card-title>
            <span class="headline">Delete User</span>
          </v-card-title>
          <v-list-item v-for="item in users" :key="item.id" @click="onDeleteUser(item)">
            <v-list-item-avatar>
              <v-avatar :color="item.color" size="40">
                <span class="headline">{{ item.name[0] }}</span>
              </v-avatar>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title v-text="item.name"></v-list-item-title>
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon>mdi-delete</v-icon>
            </v-list-item-icon>
          </v-list-item>
          <v-card-actions>
            <v-btn class="mx-auto mb-5" color="primary" @click="addUserDialog = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="settingsDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Settings</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-switch v-model="store.twelveSidedDieEnabled" label="Twelve sided die" @change="onSaveStore"></v-switch>
              <v-btn class="mx-auto" color="primary" block @click="onStartGame">Start New Game</v-btn>
            </v-container>
            <v-container>
              <v-btn class="mx-auto" color="primary" block @click="onRandomizePlayerOrder">Randomize order</v-btn>
            </v-container>
          </v-card-text>
          <v-card-text>
            <v-container>
              <div class="headline">Drag to order list</div>
            </v-container>
          </v-card-text>
          <v-container>
            <grid
                :draggable="true"
                :sortable="true"
                :items="users">
              <template slot="cell" scope="props">
                <v-list-item @change="onChangeUsersOrder(props)">
                  <v-list-item-avatar>
                    <v-avatar :color="props.item.color" size="40">
                      <span class="headline">{{ props.item.name[0] }}</span>
                    </v-avatar>
                  </v-list-item-avatar>
                </v-list-item>
              </template>
            </grid>
          </v-container>
          <v-card-actions>
            <v-btn class="mx-auto mb-5" color="primary" @click="settingsDialog = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <br>
      <div v-if="currentUser" class="d-flex justify-space-around">
        <v-avatar :color="currentUser.color" size="40">
          <span class="headline">{{ currentUser.name[0] }}</span>
        </v-avatar>
        <div>
          <v-btn min-width="150" :color="currentUser.color" :disabled="!currentUser" @click="onRoll">
            <span>Roll</span>
            <v-icon class="ml-1">mdi-dice-multiple</v-icon>
          </v-btn>
        </div>
        <div @click="onRevertLastRoll()">
          <v-icon>mdi-restore</v-icon>
        </div>
      </div>

      <ul style="display: none">
        <li>Add alert + debounce onRevertLastRoll</li>
        <li>Add alert avant de start une game</li>
      </ul>

      <div v-if="store.twelveSidedDieEnabled" style="min-height: 100px">
        <transition name="fade">
          <div v-if="diceLog.length && !rollDisabled" @click="onRoll" class="twelve-sided-die d-flex justify-center">
            <span>{{ currentDice[2] }}</span>
            <svg version=1.1 id=Layer_1 xmlns=http://www.w3.org/2000/svg xmlns:xlink=http://www.w3.org/1999/xlink x=0px y=0px viewBox="0 0 511.995 511.995" style="enable-background:new 0 0 511.995 511.995" xml:space=preserve> <g> <g> <path d="M507.807,200.272L262.474,12.859c-3.819-2.923-9.109-2.923-12.949,0L4.191,200.272c-3.627,2.773-5.077,7.509-3.648,11.84 l93.717,281.92c1.451,4.352,5.547,7.296,10.133,7.296h303.253c4.608,0,8.683-2.944,10.091-7.296l93.717-281.92 C512.885,207.803,511.413,203.046,507.807,200.272z M399.946,479.995H112.095L23.221,212.582L256.031,34.747l232.811,177.856 L399.946,479.995z"/> </g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> <g> </g> </svg>
          </div>
        </transition>
      </div>

      <div style="min-height: 150px">
        <transition name="fade">
          <div v-if="diceLog.length && !rollDisabled" @click="onRoll()" class="d-flex justify-space-around align-center">
            <svg
                xmlns="http://www.w3.org/2000/svg"
                xmlns:xlink="http://www.w3.org/1999/xlink"
                version="1.1"
                width="150"
                height="150"
                viewBox="0 0 24 24">
              <rect fill="white" x="15%" y="15%" width="70%" height="70%" />
              <path :fill="lastUser.color" :d="dice[currentDice[0] - 1]" />
            </svg>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                xmlns:xlink="http://www.w3.org/1999/xlink"
                version="1.1"
                width="150"
                height="150"
                viewBox="0 0 24 24">
              <rect fill="white" x="15%" y="15%" width="70%" height="70%" />
              <path :fill="lastUser.color" :d="dice[currentDice[1] - 1]" />
            </svg>
          </div>
        </transition>
      </div>

      <v-list style="max-height: calc(100vh - 430px);; overflow-y: auto" class="mt-3 ml-5 mr-5">
        <transition-group name="list-complete" tag="div">
          <v-list-item
              v-for="item in diceLog"
              :key="item.timestamp"
              class="list-complete-item headline">
            <v-list-item-avatar>
              <v-avatar :color="item.user.color" size="40">
                <span>{{ item.user.name[0] }}</span>
              </v-avatar>
            </v-list-item-avatar>
            <v-list-item-content>
              <div class="d-flex justify-space-around align-content-center">
                <div class="primary--text display-2">{{ item.dice[0] + item.dice[1] }}</div>
                <div v-if="store.twelveSidedDieEnabled">{{ item.dice[2] }}</div>
              </div>
            </v-list-item-content>
          </v-list-item>
        </transition-group>
      </v-list>
    </v-card>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    name: '',
    userColor: '',
    colors: [ '#F44336', '#E91E63', '#9C27B0', '#3F51B5', '#2196F3', '#00BCD4', '#009688', '#4CAF50', '#CDDC39', '#FFC107', '#FF9800', '#607D8B'],
    snackbar: false,
    snackbarText: '',
    snackbarColor: 'success',
    addUserDialog: false,
    settingsDialog: false,
    chronometer: 0,
    rollDisabled: false,
    dice: [
        'M5,3H19A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3M12,10A2,2 0 0,0 10,12A2,2 0 0,0 12,14A2,2 0 0,0 14,12A2,2 0 0,0 12,10Z',
        'M5,3H19A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3M7,5A2,2 0 0,0 5,7A2,2 0 0,0 7,9A2,2 0 0,0 9,7A2,2 0 0,0 7,5M17,15A2,2 0 0,0 15,17A2,2 0 0,0 17,19A2,2 0 0,0 19,17A2,2 0 0,0 17,15Z',
        'M5,3H19A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3M12,10A2,2 0 0,0 10,12A2,2 0 0,0 12,14A2,2 0 0,0 14,12A2,2 0 0,0 12,10M7,5A2,2 0 0,0 5,7A2,2 0 0,0 7,9A2,2 0 0,0 9,7A2,2 0 0,0 7,5M17,15A2,2 0 0,0 15,17A2,2 0 0,0 17,19A2,2 0 0,0 19,17A2,2 0 0,0 17,15Z',
        'M5,3H19A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3M7,5A2,2 0 0,0 5,7A2,2 0 0,0 7,9A2,2 0 0,0 9,7A2,2 0 0,0 7,5M17,15A2,2 0 0,0 15,17A2,2 0 0,0 17,19A2,2 0 0,0 19,17A2,2 0 0,0 17,15M17,5A2,2 0 0,0 15,7A2,2 0 0,0 17,9A2,2 0 0,0 19,7A2,2 0 0,0 17,5M7,15A2,2 0 0,0 5,17A2,2 0 0,0 7,19A2,2 0 0,0 9,17A2,2 0 0,0 7,15Z',
        'M5,3H19A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3M7,5A2,2 0 0,0 5,7A2,2 0 0,0 7,9A2,2 0 0,0 9,7A2,2 0 0,0 7,5M17,15A2,2 0 0,0 15,17A2,2 0 0,0 17,19A2,2 0 0,0 19,17A2,2 0 0,0 17,15M17,5A2,2 0 0,0 15,7A2,2 0 0,0 17,9A2,2 0 0,0 19,7A2,2 0 0,0 17,5M12,10A2,2 0 0,0 10,12A2,2 0 0,0 12,14A2,2 0 0,0 14,12A2,2 0 0,0 12,10M7,15A2,2 0 0,0 5,17A2,2 0 0,0 7,19A2,2 0 0,0 9,17A2,2 0 0,0 7,15Z',
        'M5,3H19A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3M7,5A2,2 0 0,0 5,7A2,2 0 0,0 7,9A2,2 0 0,0 9,7A2,2 0 0,0 7,5M17,15A2,2 0 0,0 15,17A2,2 0 0,0 17,19A2,2 0 0,0 19,17A2,2 0 0,0 17,15M17,10A2,2 0 0,0 15,12A2,2 0 0,0 17,14A2,2 0 0,0 19,12A2,2 0 0,0 17,10M17,5A2,2 0 0,0 15,7A2,2 0 0,0 17,9A2,2 0 0,0 19,7A2,2 0 0,0 17,5M7,10A2,2 0 0,0 5,12A2,2 0 0,0 7,14A2,2 0 0,0 9,12A2,2 0 0,0 7,10M7,15A2,2 0 0,0 5,17A2,2 0 0,0 7,19A2,2 0 0,0 9,17A2,2 0 0,0 7,15Z',
    ],
    store: {
      users: [],
      startTime: Date.now(),
      dice: [ 1, 1, 1 ],
      diceLog: [],
      twelveSidedDieEnabled: true,
    }
  }),
  computed: {
    time() {
      return this.chronometer;
    },
    users() {
      return this.store.users.slice();
    },
    currentUser() {
      return this.store.users.find(u => u.active);
    },
    lastUser() {
      return this.store.users[this.store.users.length - 1];
    },
    currentDice() {
      return this.store.dice;
    },
    diceLog() {
      return this.store.diceLog.slice();
    }
  },
  beforeMount() {
    let store = localStorage.getItem('store');
    if (store) {
      this.store = JSON.parse(store);
    }
    if (!this.store.users.length) {
      this.store.users.push({ id: this.createId(), name: 'Tom', color: this.colors[5], active: false })
    }
    this.setUsersIndex();
  },
  mounted() {
    window.clock = setInterval(function() {
      const time = Math.floor((Date.now() - this.store.startTime) / 1000);
      if (time < Number.MAX_VALUE) {
        let hours   = Math.floor(time / 3600);
        let minutes = Math.floor((time - (hours * 3600)) / 60);
        if (hours && minutes < 10) {
          minutes = '0' + minutes;
        }
        this.chronometer = hours > 0 ? (hours + ':' + minutes) : '' + minutes;
      } else {
        this.chronometer = 0;
      }
    }.bind(this), 1000);

    document.addEventListener('keyup', event => {
      if (event.code === 'Space' || event.code === 'ArrowRight') {
        this.onRoll();
      } else if (event.code === 'Backspace' || event.code === 'ArrowLeft') {
        this.onRevertLastRoll();
      }
    })
  },
  methods: {
    createId() {
      return Math.floor(Math.random() * Date.now());
    },
    shuffleArray(array = []) {
      let seed = Date.now() % 10 + 10;
      for (let x = 0; x < seed; x++) {
        for (let i = array.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * i);
          const temp = array[i];
          array[i] = array[j];
          array[j] = temp;
        }
      }
      return array;
    },
    onSaveStore() {
      localStorage.setItem('store', JSON.stringify(this.store));
    },
    onStartGame() {
      this.store.dice = [1, 1, 1];
      this.store.diceLog = [];
      this.store.startTime = Date.now();

      this.setUsersIndex();
      this.onSaveStore();
    },
    onRoll() {
      if (this.rollDisabled) {
        return;
      }
      this.rollDisabled = true;

      const id = this.createId();
      const timestamp = Date.now();
      const user = this.store.users.shift();
      this.store.users.push(user);

      const die1 = 1 + Math.floor(Math.random() * 6);
      const die2 = 1 + Math.floor(Math.random() * 6);
      const die3 = 1 + Math.floor(Math.random() * 12);

      const dice = [ die1, die2, die3 ];
      this.store.dice = dice.slice();
      this.store.diceLog.unshift({ id, timestamp, user, dice });
      this.setUsersIndex();
      this.onSaveStore();

      setTimeout(function() {
        this.rollDisabled = false;
      }.bind(this), 500);
    },
    onRevertLastRoll() {
      if (this.rollDisabled) {
        return;
      }
      this.rollDisabled = true;

      this.store.diceLog.shift();
      if (this.store.diceLog.length) {
        this.store.dice = this.store.diceLog[0].dice;
      }

      const users = this.store.users.slice();
      const user = users.pop();
      users.unshift(user);

      this.store.users = users;
      this.setUsersIndex();
      this.onSaveStore();

      setTimeout(function() {
        this.rollDisabled = false;
      }.bind(this), 1500);
    },
    onAddUser() {
      const id = this.createId();
      const name = this.name && (this.name[0].toUpperCase() + this.name.slice(1, 100));
      const color = this.userColor;

      let error = '';

      if (!name)
        error += ' - name';
      if (!color)
        error += ' - color';
      if (this.store.users.find(u => u.name === name))
        error += ' - Name already taken';

      if (error) {
        this.snackbar = true;
        this.snackbarText = 'Error ' + error;
        this.snackbarColor = 'error';
        return;
      }

      this.store.users.push({ id, name, color, active: false });
      this.onSaveStore();

      this.name = '';
      this.userColor = '';
      this.addUserDialog = false;
    },
    onDeleteUser(user) {
      this.store.users = this.store.users.filter(u => u.id !== user.id);
      this.onSaveStore();
    },
    onRandomizePlayerOrder() {
      this.store.users = this.shuffleArray(this.store.users.slice());
    },
    onChangeUsersOrder(data) {
      const user = this.store.users.splice(data.index, 1)[0];
      this.store.users.splice(data.sort, 0, user);
      this.setUsersIndex();
      this.onSaveStore();
    },
    setUsersIndex() {
      this.store.users.forEach((u, index) => {
        u.index = index;
        u.active = index === 0;
      });
    }
  }
};
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

/* LIST ANIMATION START */
.list-complete-item {
  transition: all 2s;
}
.list-complete-enter, .list-complete-leave-to
  /* .list-complete-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(-70px);
}
.list-complete-leave-active {
  position: absolute;
}
/* LIST ANIMATION END */

.main-container {
  height: 100%;
}
@media screen and (min-width: 850px) {
  .main-container {
    margin: auto;
    width: 750px;
  }
}
.twelve-sided-die {
  position: relative;
  margin-top: 35px;
  margin-bottom: 15px;
}
.twelve-sided-die svg {
  margin: auto;
  height: 100px;
  fill: white;
}
.twelve-sided-die span {
  position: absolute;
  top: 35px;
  font-size: 35px;
